@import "https://cdn.jsdelivr.net/npm/bulma@0.8.0/css/bulma.min.css";
@import "http://fonts.googleapis.com/css?family=Noto+Serif+TC";
@import "http://fonts.googleapis.com/css?family=Noto+Sans+TC";
@import "http://cdn.materialdesignicons.com/5.0.45/css/materialdesignicons.min.css";

* {
    font-family: 'Noto Sans', 'Noto Sans CJK TC', sans-serif;
}


.jump {
    transition: margin-top 0.5s;
    transition: margin-bottom 0.2s;

    &:hover {
        margin-bottom: 0.25rem;
    }

    &:active {
        margin-top: 0.25rem;
    }

}

@media only screen and (max-width: 768px) {

    .home {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 4rem repeat(8, 1fr);
        min-width: 100vw;
        max-width: 100vw;
        min-height: 100vh;
        max-height: 100vh;

        .top-panel {
            grid-row: 1/2;
            grid-column: 1/1;
            border-bottom: 0.1rem solid lightgrey;
        }

        .body-section {
            grid-row: 2/10;
            grid-column: 1/1;
            min-height: 100%;
            max-height: 100%;
            overflow-y: scroll;
        }

        .bottom-panel {
            grid-row: 9/10;
            grid-column: 1/1;
            place-self: center;
            z-index: 2;
        }

        .opt_panel {
            display: none
        }
    }
}

@media only screen and (min-width: 768px) {

    .home {
        display: grid;
        grid-template-columns: minmax(auto 240px) repeat(12, 1fr);
        grid-template-rows: 4rem repeat(8, 1fr);
        min-width: 100vw;
        max-width: 100vw;
        min-height: 100vh;
        max-height: 100vh;

        .top-panel {
            grid-row: 1/2;
            grid-column: 1/2;
        }

        .body-section {
            grid-row: 1/10;
            grid-column: 2/14;
            max-height: 100%;
            justify-self: stretch;
            align-self: stretch;
            overflow-y: scroll;
        }

        .bottom-panel {
            grid-row: 9/10;
            grid-column: 1/2;
            margin: 2rem;
            place-self: center;
        }

        .opt_panel {
            grid-row: 2/9;
            grid-column: 1/2;
            justify-self: stretch;
            align-self: stretch;
            padding: 2rem;
        }
    }

}